              <a href=""><li></li></a>
              <a href=""><li></li></a>
              <a ><li></li></a>
              <a href=""><li></li></a>
              <a href=""><li></li></a>
            </ul>
            <em></em>
            <span></span>
          </div>
        </li>
        <li class="ui-col ui-txt-c">
          <a href=""></a>
        </li>
        <li class="ui-col ui-txt-c">
          <router-link to="/user"></router-link>
        </li>
      </ul>
    </footer>
</template>

<script>

export default {
  name: 'foot',
  data () {
    return {
      isListShow:false,
    }
  },
  computed : {
      styleObject(){
          return {bottom: this.isListShow ? '54px' : '-9999px'}
      }
  }
}
</script>

